	module.exports = {"data":{"wordpressPost":{"title":"Emma Roberts Chopped Her Hair And It&#8217;s So Good We Want To Do It Too","content":"<p>Oh that <a href=\"https://www.instagram.com/emmaroberts/\">Emma Roberts</a>. She&#8217;s too cute for words. She&#8217;s also smart and funny and a damn fine actress.</p>\n<p>That&#8217;s all very cool and yes, it makes her sound pretty awesome but you know what we love most about her? Her hair flair! The Scream Queens actress mixes it up and regularly. Just this year she&#8217;s rocked a <a href=\"http://the-beautyinsider.com/emma-roberts-killer-new-hairstyle/\">killer red</a>, a very join trend smoky quartz, a rich chocolate and now &#8211; a dirty blonde and frankly, we cannot get enough.</p>\n<figure id=\"attachment_2828\" style=\"width: 300px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2828\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06224248/JPG-Emma-Blonde-300x297.jpg\" alt=\"\" width=\"300\" height=\"297\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06224248/JPG-Emma-Blonde-300x297.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06224248/JPG-Emma-Blonde-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06224248/JPG-Emma-Blonde.jpg 595w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@nikkilee901</figcaption></figure>\n<p>&nbsp;</p>\n<p>The daring actress and niece of the famous Julia Roberts (yes &#8211; we&#8217;re talking Hollywood royalty here) also opted for a bold new cut with a textured, rough-chopped lob. That&#8217;s a long bob for the uninitiated &#8211; long bob = lob. Get it?</p>\n<p>Anyway, it looks incredible and we want one. Now.</p>\n<p>Maybe you too. If so, there&#8217;s a few things to consider before you settle into the salon chair, says<a href=\"http://rawhair.com.au\"> Anthony Nader, at RAW</a> in Sydney&#8217;s Surry Hills.</p>\n<p>Is your hair the right texture for this style? According to Anthony this cut works best on hair that&#8217;s at least medium thickness. Fine girls &#8211; sorry. There are better options for you.</p>\n<figure id=\"attachment_2829\" style=\"width: 300px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2829\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06224442/JPG-Emma-blonde-garden-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06224442/JPG-Emma-blonde-garden-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06224442/JPG-Emma-blonde-garden-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06224442/JPG-Emma-blonde-garden.jpg 593w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@emmaroberts</figcaption></figure>\n<p>&nbsp;</p>\n<p>Is your hair seriously curly? You can still get this cut but if you want it to look like Emma&#8217;s street-urchin, choppy style then you&#8217;re looking at blow-drying and possibly straightening too. Are you up for it?</p>\n<p>Finally, if you&#8217;re wanting to rock a dirty blonde and you&#8217;re not naturally fair, you&#8217;re looking at regular upkeep. Seriously. You&#8217;ll be in the salon once a month &#8211; at least.</p>\n<p>Still keen? Then go for it. This is rock chic at its finest and who doesn&#8217;t love that?</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06224206/JPG-Emma-jacket.jpg"},"categories":[{"slug":"hair","name":"Hair"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"214432a2-4c18-5d62-b802-f23545196de5"}}

